<template>
    <div class="notes container">
        <div class="notes__top">
            <h2 class="notes__top_title">{{ notes.length > 0 ? $t('AllNotes') : $t('NoNotes') }}</h2>
            <button @click="view = !view" class="notes__top_btn">

                <img v-show="view" src="@/assets/img/list.svg" alt="">
                <img v-show="!view" src="@/assets/img/layout.svg" alt="">

                <span>{{ view ? 'Список' : 'Сетка' }}</span>
            </button>
        </div>

        <div class="notes__list" :class="{active: !view}">
            <NotesItem v-for="note in notes" :key="note.id" :note="note" :view="view" @delNote="$emit('delNote', note.id)" @change="$emit('change', note.id)"/>
            
        
            
        </div>
    </div>
</template>

<script>
import NotesItem from '@/components/NotesItem.vue';

export default {
    props: {
        notes: {
            typeof: Array
        }
    },

    data() {
        return {
            view: true,
        };
    },
    components: { NotesItem }
}
</script>

<style lang="scss" scoped></style>