<template>
   <header class="header">
    <nav v-if = "nav" class="nav">
        <button class="nav__lang" @click="switchLang">{{ $i18n.locale }}</button>
        
        <h1 class="nav__title">{{ $t('Notes') }}</h1>
        <button class="nav__search" @click="nav = false"><img src="@/assets/img/search.svg" alt=""></button>
    </nav>
    
    <nav v-else class="nav__search">
        <button class="nav__search_back" @click="nav = true, search = ''">
            <img src="@/assets/img/leading-iconback.svg" alt="">
        </button>
        
        <input  v-model="search" type="text" class="nav__search_input" :placeholder="$t('Search')">
        
        <button class="nav__search_clear" @click="search  = ''">
            <img src="@/assets/img/iconcancel.png" alt="">
        </button>
    </nav>
   </header>
   
</template>




<script>

export default {
   data() {
    return {
        nav: true,
        search:''
    }
   },
   
   watch: {
    search(newVal){
        this.$emit('setSearch', newVal)
    }
   },
   
   props: {
    filterNote: {
        typeof: Object
    }
   },
   
   methods: {
    switchLang() {
        this.$i18n.locale == 'ru' ? this.$i18n.locale = 'eng' : this.$i18n.locale = 'ru'
        
        localStorage.lang = this.$i18n.locale
    }
   },
   
   
}

</script>

