<template>
    <Transition name="modal">



        <div class="modal" @click="closeModal">

            <div class="modal__block" @click.stop>
                <h2 class="modal__block_title">{{edit ? 'Изменить заметку' : 'Добавить заметку'}}</h2>
                <div class="modal__inputs">
                    <label>
                        <span>
                            Title
                        </span>
                        <input type="text" v-model="title">
                    </label>
                    <label>
                        <span>
                            Content
                        </span>
                        <textarea v-model="text"></textarea>
                    </label>
                </div>
                <div class="modal__block_btns">
                    <button class="modal__block_btns-btn del" @click="closeModal">Отмена</button>
                    <button v-if="!edit" class="modal__block_btns-btn edit" @click="addNote">Добавить</button>
                    <button v-if="edit" class="modal__block_btns-btn edit" @click="change">Изменить</button>
                </div>
            </div>

        </div>

    </Transition>
</template>

<script>

import { v4 as uuidv4 } from "uuid";
export default {
    
    data() {
        return {
            title: '',
            text: '',
        }
    },
    
    
    methods: {
        closeModal() {
            this.$emit('close')
            this.title = this.text = ''
        },
        
        addNote() {
            if (this.text != '' && this.title != '') {
            const note = {
                id: uuidv4(),
                title: this.title,
                text: this.text,
                date: new Date().toLocaleDateString()
            }
            
                this.$emit('addNote', note)
                this.closeModal()
            }
        },
        
        change() {
            const newEditedNote = {
                id: this.editedNote.id,
                title: this.title,
                text: this.text,
                date: new Date().toLocaleDateString() 
            }
            this.$emit('changedNote', newEditedNote)
            this.closeModal()
        }
    },
    
    props:{
        edit: {
            typeof: Boolean
        },
        editedNote: {
            typeof: Object
        }
    }
}
</script>

<style></style>