<template>
  <Navbar :notes="filterNotes" @setSearch="searchValue = $event"/>
  <Notes :notes="filterNotes" @delNote="delNote" @change="changeNote"/>
  
  <Modal v-show="isModalOpen" 
  @close="isModalOpen = false" 
  @addNote="addNote"
  :edit="edit"
  :editedNote = "editedNote"
  @changedNote="changedNote"
  />

  <buttton class="addNote" @click="isModalOpen = true, edit = false" v-show="!isModalOpen">
    <img src="@/assets/img/pens.svg" alt="">
  </buttton>
</template>

<script>
import Navbar from '@/components/Navbar.vue'
import Notes from '@/components/Notes.vue'
import Modal from './components/Modal.vue'


export default {
  data() {
    return {
      notes: [],
      isModalOpen: false,
      // text: ''
      edit: false,
      editedNote: null,
      searchValue: '',
    }
  },
  
  components: {
    Navbar,
    Notes,
    Modal,
  },

  mounted() {
    this.getNotes()
  },

  methods: {
    addNote(note) {
      this.notes.push(note)
    },

    getNotes() {
      let localNotes = localStorage.notes

      if (localNotes) {
        this.notes = JSON.parse(localNotes)
      }
    },

    delNote(id) {
      this.notes.splice(id, 1)
    },
    
    changeNote(id){
      this.isModalOpen = this.edit = true
      
      let currentNote = this.notes.find(note => note.id == id)
      
      this.editedNote = currentNote
    },
    changedNote(newNote){
      this.notes.forEach(note => {
        if (note.id == newNote.id) {
          note.title = newNote.title
          note.text = newNote.text
          note.date = newNote.date
        }
      })
    }

  },

  watch: {
    // text(newVal, oldVal) {

    // }

    notes: {
      handler() {
        localStorage.notes = JSON.stringify(this.notes)

        
      },
      deep: true
    }
  },
  computed: {
    filterNotes(){
      return this.searchValue ? this.notes.filter(note => note.title.toLowerCase().includes(this.searchValue.toLowerCase())) : this.notes
    }
  }
}
</script>



